export default {
	name: 'basics',
	data() {
		return {
			showQiPao:false,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			currentSwiper:0,
			indicatorDots: false,
			autoplay: false,
			interval: 2000,
			duration: 500,
			tarHeight: 50,
			isRight: true,
			screenHeight: 0,
			mapHeight: 500,
			contentheght: 110,
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [
				{
					latitude: 39.909,
					longitude: 116.39742,
					iconPath: '../../static/location.png',
					callout: {
						content: '无法服务，附近暂无技师',
						bgColor: '#d81e06',
						borderColor: '#d81e06',
						borderRadius: 50,
						display: 'ALWAYS',
						padding: 10,
						fontSize: 15,
						color: '#fff'
					}
				}
			],
			myCarList:[
				{
					cars:[
						"车辆1","车辆2","车辆3"
					]
				},
				{
					cars:[
						"车辆4","车辆5","车辆6"
					]
				}
			]
		};
	},
	onShow() {
		console.log('success');
	},

	methods: {
		//跳转添加车辆页面
		toAddCarPage(){
			let url = '/pages/washcar/addCar';
			uni.navigateTo({
				url:url
			})
		},
		//上一页
		preSwiper(){
			this.currentSwiper --;
		},
		//下一页
		nextSwiper(){
			this.currentSwiper ++;
		},
		// 监听swiper 改变
		swiperChange(e){
			console.log("当前:"+e.detail.current)
			console.log("原始:"+e.detail.source)
		},
		//选择车辆地址
		doSelectLocations() {
			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
				}
			});
		},
		//选择车辆
		doSelectCars() {
			this.isRight = !this.isRight;
			if (this.isRight) {
				this.contentheght = 110;
			} else {
				this.contentheght = 220;
			}
			this.mapHeight = this.screenHeight - this.contentheght;
		}
	},
	mounted() {
		this.screenHeight = uni.getStorageSync('screenHeight') - 50 - uni.getStorageSync('bottomTarHeight');
		this.mapHeight = this.screenHeight - this.contentheght;
	}
};
